// const class names
const className = 'slider',
    slidesContainerClass = '.slider__slides',
    activeSlideClass = 'slider__slide--active',
    sliderArrowNextClass = 'arrow-right--btn-next',
    //sliderArrowPrevClass = 'arrow-left--btn-prev',
    numberedIndicatorTotalSlides =
        '.slider__numbered-position-indicator__total-slides',
    numberedIndicatorActiveSlide =
        '.slider__numbered-position-indicator__active-slide',
    //progressIndicatorProgress =
    //'.slider__progress-position-indicator__progress',
    sliderTargetAttr = 'slider-target';

function slider() {
    // get all nav arrows
    let sliderArrows = document.querySelectorAll('.slider-arrow');

    // configure click handlers for all next/prev
    sliderArrows.forEach(function(sliderArrow) {
        sliderArrow.addEventListener('click', handleSlideChange, false);
    });

    setTotalSlidesIndicator();

    //setProgressIndicator();
}

function getActiveSliderChildren(activeSliderClass) {
    // combine target with container so we can target the container itself
    let activeSlidesContainerClassText =
        activeSliderClass + ' ' + slidesContainerClass;

    // get the node collection
    let slidesCollection = document.querySelector(
        activeSlidesContainerClassText
    ).children;

    return slidesCollection;
}

function getSliderCollectionLength(slidesCollection) {
    // set length for use later
    let sliderCollectionLength = slidesCollection.length;

    return sliderCollectionLength;
}

function getCurrentSlideIndex(currentSlideIndexElement) {
    // convert to number string to int
    let slideIndex = parseInt(currentSlideIndexElement.textContent);

    return slideIndex;
}

function getCurrentSlideIndexElement(activeSliderTargetAttr) {
    // build the class name of slide index element
    let currentSlideIndexClassText =
        activeSliderTargetAttr + ' ' + numberedIndicatorActiveSlide;

    // get the element
    let currentSlideIndexElement = document.querySelector(
        currentSlideIndexClassText
    );

    return currentSlideIndexElement;
}

function getActiveSlide(activeSliderTargetAttr) {
    // get active slide text
    let activeSlideClassText = activeSliderTargetAttr + ' .' + activeSlideClass;

    // get the active slide of this slider
    let activeSlide = document.querySelector(activeSlideClassText);

    return activeSlide;
}

function handleSlideChange(e) {
    let activeSliderClass,
        slidesCollection,
        sliderCollectionLength,
        currentSlideIndexElement,
        slideIndex,
        activeSlide;

    // get slider target attr - e.g '.slider--testimonial'
    activeSliderClass = e.target.getAttribute(sliderTargetAttr);

    // get children of active slider
    slidesCollection = getActiveSliderChildren(activeSliderClass);

    // get the length of this slider's children
    sliderCollectionLength = getSliderCollectionLength(slidesCollection);

    // get current index element
    currentSlideIndexElement = getCurrentSlideIndexElement(activeSliderClass);

    // get the index as an int
    slideIndex = getCurrentSlideIndex(currentSlideIndexElement);

    // get active slide for this slider
    activeSlide = getActiveSlide(activeSliderClass);

    // toggle next/prev sibling and maintain index
    if (e.target.classList.contains(sliderArrowNextClass)) {
        if (slideIndex !== sliderCollectionLength) {
            slideIndex++;
            activeSlide.classList.remove(activeSlideClass);
            activeSlide.nextElementSibling.classList.add(activeSlideClass);
        }
    } else {
        if (slideIndex !== 1) {
            slideIndex--;
            activeSlide.classList.remove(activeSlideClass);
            activeSlide.previousElementSibling.classList.add(activeSlideClass);
        }
    }

    setButtonAttributes(e.target, sliderCollectionLength, slideIndex);
    setActiveSlideIndex(currentSlideIndexElement, slideIndex);
    // setProgressIndicator();
}

function setTotalSlidesIndicator() {
    // get all sliders
    let allSliders = document.querySelectorAll('.slider');

    // target each slider and get children collection
    allSliders.forEach(function(slider) {
        let slidesCollection,
            slidesCollectionLength,
            totalSlidesIndicatorClassText,
            totalSlidesIndicator;

        // get the children
        slidesCollection = getActiveSliderChildren('.' + slider.classList[1]);

        // get the length of this slider's children
        slidesCollectionLength = getSliderCollectionLength(slidesCollection);

        // set class text to target element
        totalSlidesIndicatorClassText =
            '.' + slider.classList[1] + ' ' + numberedIndicatorTotalSlides;

        // get the element
        totalSlidesIndicator = document.querySelector(
            totalSlidesIndicatorClassText
        );

        // set the length text
        totalSlidesIndicator.textContent = slidesCollectionLength;
    });
}

function setActiveSlideIndex(currentSlideIndexElement, slideIndex) {
    currentSlideIndexElement.textContent = slideIndex;
}
